import { alpha, useTheme, experimentalStyled as styled } from '@material-ui/core/styles';
import { Box, Grid, Card, Container, Typography, TextField, FormControl, Button, } from '@material-ui/core';
import { varFadeInUp, MotionInView, varFadeInDown } from '../../animate';

export default function ContactForm() {
    return (
        <>
            <Box sx={{ pt: {xs: 10, lg: 0 }, pb: {xs: 8, lg: 8 }  }}>
                <Container maxWidth="lg">
                    <Grid container spacing={2} justifyContent="center">
                        <Grid item xs={10}>
                            <MotionInView variants={varFadeInDown}>
                                <Typography variant="h2">
                                Contact Us
                                </Typography>
                                <Typography component="p" className="sb_ttl_txt text-left ml-0" sx={{ mb: {sx: 3, lg: 5 }, }}>
                                    Leave your message here and we will get back to you within 24hrs.
                                </Typography>
                            </MotionInView>
                        </Grid>
                        <Grid item xs={12} md={10}>
                            <MotionInView variants={varFadeInUp}>
                                <Box sx={{ pt: {xs: 5, lg: 0 }, pb: {xs: 4 }  }}>
                                    <Grid container spacing={3} className="def_form">
                                        <Grid item xs={12} md={6}>
                                            <FormControl>
                                                <TextField
                                                id=""
                                                label="First Name"
                                                style={{ margin: 8 }}
                                                placeholder="Enter first name here"
                                                fullWidth
                                                margin="normal"
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid item xs={12} md={6}>
                                            <FormControl>
                                                <TextField
                                                id=""
                                                label="Last Name"
                                                style={{ margin: 8 }}
                                                placeholder="Enter last name here"
                                                fullWidth
                                                margin="normal"
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid item xs={12} md={6}>
                                            <FormControl>
                                                <TextField
                                                id=""
                                                label="Company Name"
                                                style={{ margin: 8 }}
                                                placeholder="Enter company name here"
                                                fullWidth
                                                margin="normal"
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid item xs={12} md={6}>
                                            <FormControl>
                                                <TextField
                                                id=""
                                                label="Designation"
                                                style={{ margin: 8 }}
                                                placeholder="Enter designation here"
                                                fullWidth
                                                margin="normal"
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid item xs={12} md={6}>
                                            <FormControl>
                                                <TextField
                                                id=""
                                                label="Email"
                                                style={{ margin: 8 }}
                                                placeholder="Enter email here"
                                                fullWidth
                                                margin="normal"
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid item xs={12} md={6}>
                                            <FormControl>
                                                <TextField
                                                id=""
                                                label="Phone"
                                                style={{ margin: 8 }}
                                                placeholder="Enter phone here"
                                                fullWidth
                                                margin="normal"
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <FormControl>
                                                <TextField
                                                id=""
                                                label="Message"
                                                placeholder="Enter your message here"
                                                fullWidth
                                                margin="normal"
                                                multiline
                                                rows={3}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                />
                                            </FormControl>
                                        </Grid>
                                        <Grid item xs={12} className="text-center">
                                            <Button
                                                size="large"
                                                variant="contained"
                                                className="bnr_btn bnr_btn_v2"
                                            >
                                                Send message
                                            </Button>
                                        </Grid>
                                    </Grid>
                                </Box>
                                
                            </MotionInView>
                        </Grid>
                        
                    </Grid>
                </Container>
            </Box>
        </>
    );
}

