// material
import { experimentalStyled as styled } from '@material-ui/core/styles';
import InsScOne from 'components/_external-pages/initiatives/InsScOne';
// components
import Page from '../components/Page';
import {
    ContactForm,
  LandingHero,  
  
} from '../components/_external-pages/contact-page';

// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
  height: '100%'
}));

const ContentStyle = styled('div')(({ theme }) => ({
  overflow: 'hidden',
  position: 'relative',
  backgroundColor: theme.palette.background.default
}));

// ----------------------------------------------------------------------

export default function ContactPage() {
  return (
    <RootStyle title="Contact Us" id="move_top">
      <LandingHero />
        <ContentStyle>
            <ContactForm />
        </ContentStyle>
    </RootStyle>
  );
}

